




























export default {
	data() {
		return {
			toast:false
		};
	},
	methods:{
		toBind(){
			this.toast=true
		},
		cancel(){
			this.toast=false
		},
		confirm(){
			uni.login({
				provider: 'weixin',
				success: function (loginRes) {
					console.log(loginRes.authResult);
					this.toast=false
				}
			});
		},
	}
}



